'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { collection, addDoc, Timestamp, getDocs } from 'firebase/firestore';
import { db } from '@/lib/firebase-client';
import { COLLECTION_NAMES, Competency, RatingScore } from '@/types';
import { CompetencyConverter } from '@/lib/converters';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from '@/components/ui/dialog';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import { Button } from '@/components/ui/button';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Plus } from 'lucide-react';

// Hardcoded for POC
const EDUCATOR_ID = 'demo-educator-123';
const ORG_ID = 'demo-org-456';

interface AddCompetencyRatingProps {
  learnerId: string;
}

export function AddCompetencyRating({ learnerId }: AddCompetencyRatingProps) {
  const router = useRouter();
  const [open, setOpen] = useState(false);
  const [loading, setLoading] = useState(false);
  const [competencies, setCompetencies] = useState<Competency[]>([]);
  const [formData, setFormData] = useState({
    competency_id: '',
    score: '' as '' | RatingScore,
    comment: '',
  });

  // Fetch competencies when modal opens
  useEffect(() => {
    if (open && competencies.length === 0) {
      const fetchCompetencies = async () => {
        const competenciesRef = collection(db, COLLECTION_NAMES.competencies).withConverter(
          CompetencyConverter
        );
        const snapshot = await getDocs(competenciesRef);
        setCompetencies(snapshot.docs.map((doc) => doc.data()));
      };
      fetchCompetencies();
    }
  }, [open, competencies.length]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);

    try {
      // Create a completed mentor rating
      await addDoc(collection(db, COLLECTION_NAMES.ratings), {
        learner_id: learnerId,
        competency_id: formData.competency_id,
        rater_id: EDUCATOR_ID,
        rater_type: 'mentor',
        score: Number(formData.score),
        status: 'completed',
        comment: formData.comment || undefined,
        created_at: Timestamp.now(),
      });

      // Reset form and close modal
      setFormData({ competency_id: '', score: '', comment: '' });
      setOpen(false);

      // Refresh the page to show new rating
      router.refresh();
    } catch (error) {
      console.error('Error adding competency rating:', error);
      alert('Failed to add rating. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogTrigger asChild>
        <Button className="w-full sm:w-auto">
          <Plus className="w-4 h-4 mr-2" />
          Add Competency Rating
        </Button>
      </DialogTrigger>
      <DialogContent className="sm:max-w-[525px]">
        <DialogHeader>
          <DialogTitle>Add Competency Rating</DialogTitle>
          <DialogDescription>
            Assign a competency to this learner with an initial mentor rating.
          </DialogDescription>
        </DialogHeader>
        <form onSubmit={handleSubmit} className="space-y-4 mt-4">
          <div className="space-y-2">
            <Label htmlFor="competency">Competency</Label>
            <Select
              value={formData.competency_id}
              onValueChange={(value) => setFormData({ ...formData, competency_id: value })}
              required
            >
              <SelectTrigger>
                <SelectValue placeholder="Select a competency" />
              </SelectTrigger>
              <SelectContent>
                {competencies.map((competency) => (
                  <SelectItem key={competency.id} value={competency.id}>
                    {competency.title}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

          <div className="space-y-2">
            <Label htmlFor="score">Initial Rating (0-4)</Label>
            <Select
              value={formData.score.toString()}
              onValueChange={(value) =>
                setFormData({ ...formData, score: Number(value) as RatingScore })
              }
              required
            >
              <SelectTrigger>
                <SelectValue placeholder="Select rating" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="0">0 - No evidence</SelectItem>
                <SelectItem value="1">1 - Beginning</SelectItem>
                <SelectItem value="2">2 - Developing</SelectItem>
                <SelectItem value="3">3 - Proficient</SelectItem>
                <SelectItem value="4">4 - Expert</SelectItem>
              </SelectContent>
            </Select>
          </div>

          <div className="space-y-2">
            <Label htmlFor="comment">Comment (optional)</Label>
            <Textarea
              id="comment"
              placeholder="Add notes about this rating..."
              value={formData.comment}
              onChange={(e) => setFormData({ ...formData, comment: e.target.value })}
              rows={3}
            />
          </div>

          <div className="flex justify-end gap-3 mt-6">
            <Button
              type="button"
              variant="outline"
              onClick={() => setOpen(false)}
              disabled={loading}
            >
              Cancel
            </Button>
            <Button type="submit" disabled={loading}>
              {loading ? 'Adding...' : 'Add Rating'}
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}
